export class DirectionsTravelMode extends woosmap.utils.MVCObject {
  constructor(directionsProvider) {
    super();
    this.directionsProvider = directionsProvider;
    this.selectedTravelMode = "DRIVING";
    this.initTravelMode();
    this.updateTravelMode();
  }

  initTravelMode() {
    document.querySelectorAll(".travel-mode").forEach((travelMode) => {
      travelMode.addEventListener(
        "click",
        (e) => {
          document
            .querySelectorAll(".travel-mode")
            .forEach((el) => el.classList.remove("selected"));
          travelMode.classList.add("selected");
          this.selectedTravelMode = travelMode.dataset.modevalue;
          this.updateTravelMode();
        },
        this
      );
    }, this);
  }

  updateTravelMode() {
    //this.set('selectedTravelMode', this.selectedTravelMode);
    this.directionsProvider.set("selectedTravelMode", this.selectedTravelMode);
  }
}
